import router from './router'
import axios from 'axios'
const _import = require('./router/_import_' + process.env.NODE_ENV) //
import Layout from '@/views/layout' //Layout 
import errorPage from '@/views/errorPage' //Layout 
import Login from '@/views/login' //Layout 

import NProgress from 'nprogress'
import 'nprogress/nprogress.css'

import staticRouter from './router/staticRouter'
import asyncRouter from './router/asyncRouter';
const staticFile = {
  login:Login,
  errorPage:errorPage
}

var getRouter; //
var isChangeRoute = false;

// fakeRouter
let fakeRouter = {"router": asyncRouter}

if(!getObjArr("fakeRouter")){
  isChangeRoute = true;
  saveObjArr('fakeRouter',JSON.stringify(fakeRouter))
}else if(getObjArr("fakeRouter") != JSON.stringify(fakeRouter)){
  isChangeRoute = true;
  saveObjArr('fakeRouter',JSON.stringify(fakeRouter))
}else{
  isChangeRoute = false;
}


const importAll =(r)=>{
  let cache=  {};
  r.keys().forEach(key=>cache[key] = r(key))
  return cache
}

const allFile = importAll(require.context("@/views/",true,/\.vue/))

var fileAarray = []
for (const key in allFile) {
  var filePathIsArray = Array.isArray(allFile)
  var filePath = filePathIsArray?allFile[key].default.__file:key;
  var filePathR = '';
  if(filePathIsArray){
    filePathR = filePath.replace(/\\/g,"/");
  }else{
    filePathR = filePath.replace("./","")
  }
  var filePathL = filePathR.replace("src/views/","").replace(/\.vue/,"");
  fileAarray.push(filePathL)
}

router.beforeEach((to, from, next) => {
  if (!getRouter) { //
    if (!getObjArr('router') || isChangeRoute) {
      // easy-mock,,axios
      // axios.get('https://www.easy-mock.com/mock/5a5da330d9b48c260cb42ca8/example/antrouter').then(res => {
      getRouter = fakeRouter.router //
      getRouter = [...getRouter,...staticRouter]
      saveObjArr('router', getRouter) //localStorage

      routerGo(to, next) //
      isChangeRoute = false;
      // })
    } else { //localStorage
      getRouter = getObjArr('router') //
      routerGo(to, next)
      isChangeRoute = false;
    }
  } else {
    isChangeRoute = false;
    NProgress.start()
    document.title = to.meta.title + '- ';
    next()
  }

})

function routerGo(to, next) {
  NProgress.start()
  getRouter = filterAsyncRouter(getRouter) //
  router.addRoutes(getRouter) //
  global.antRouter = getRouter //
  next({ ...to, replace: true })
}

function saveObjArr(name, data) { //localStorage 
  localStorage.setItem(name, JSON.stringify(data))
}

function getObjArr(name) { //localStorage 
  return JSON.parse(window.localStorage.getItem(name));
}

function filterAsyncRouter(asyncRouterMap) { //
  const accessedRouters = asyncRouterMap.filter(route => {
    if (route.component) {
      if (route.component === 'Layout') { //Layout
        route.component = Layout
      } else if (staticFile[route.component]) { //Layout
        route.component = staticFile[route.component]
      } else {
        // console.log(route.component,' ====== ',fileAarray.includes(route.component))
        route.component = fileAarray.includes(route.component)?_import(route.component):errorPage
      }
    }
    if (route.children && route.children.length) {
      route.children = filterAsyncRouter(route.children)
    }
    return true
  })

  return accessedRouters
}
